import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';

import Definition from '../components/DefinitionDialog.jsx'

import {large_threshold} from '../files/constants.js'

//Image imports

import lab_coiling_img from '../images/labeled_coiling.svg'
import lab_clipping_img from '../images/labeled_clipping.svg'
import clip_v_coil_img from '../images/clip_v_coil.svg'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

class AboutSurg extends React.Component {
  constructor(props) {

    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold,
      coilExp: false,
      clipExp: false,
      showStrokeDefinition: false
    }
  }

  componentWillMount() {
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);
  }

  toggleCoilExp() {
    this.setState({
      coilExp: !this.state.coilExp
    });
  }
  toggleClipExp() {
    this.setState({
      clipExp: !this.state.clipExp
    });
  }

  handleStrokeDefinitionClose = () => {
    this.setState({showStrokeDefinition: false})
  }
  handleStrokeDefinitionOpen = () => {
    this.setState({showStrokeDefinition: true})
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : ''
    return (<div>
      <Grid container={true} spacing={16} className={'norm_padding'}>
        <Paper>
          <Grid container={true} spacing={16} className={'norm_padding'} direction={'column'}>
            <Grid className={'content-block summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                ? 'row-reverse'
                : 'column'} spacing={16}>

              <Grid item={true} xs={12} sm={6}>
                <img src={clip_v_coil_img} className={size_class}/>
              </Grid>
              <Grid item={true} xs={12} sm={6} alignItems={'center'} container={true}>
                <div className={size_class + ' summary-block-text'}>
                  <p className={'supertitle'}>Treatment:</p>
                  <Typography variant='title' gutterBottom={true}>Need to know: aneurysm surgery</Typography>
                  <Typography variant='body1' paragraph={true}>If an anuerysm is to be treated with surgery, there are 2 approaches to doing so.
                    <ul>
                      <li>Endovascular surgery. Also called
                        <b>'coiling.'</b>
                      </li>
                      <li>Open surgery. Also called
                        <b>'clipping.'</b>
                      </li>
                    </ul>
                    Choosing between clipping and coiling can be complicated depending on your medical history and aneurysm, although sometimes one will clearly be a better option. Your doctor will help you understand which is best for you.
                  </Typography>
                </div>
              </Grid>
            </Grid>
            <Divider/>
            <Grid item={true} xs={12} className={'content-block'}>
              <Typography variant={'title'} gutterBottom={'true'}>There are 2 surgical options:</Typography>
              <Typography variant={'body1'} paragraph={true}>Your provider will determine which option is better for you.</Typography>
              <Grid item={true} xs={12} container={true} spacing={16}>
                <Grid item={true} xs={12} sm={6}>
                  <Paper className={'norm_padding'}>
                    <Typography variant={'title'} gutterBottom={'true'}>Endovascular Surgery</Typography>
                    <Typography variant={'subtitle1'} gutterBottom={'true'}>A surgery done from within the blood vessels.</Typography>
                    <img src={lab_coiling_img}/>
                    <Grid alignItems={'center'} container={true} justify={'space-between'}>
                      <Typography variant={'body1'}>
                        <b>Procedure:</b>
                        takes about 45 minutes.
                      </Typography>
                      <Button onClick={() => this.toggleCoilExp()}><ExpandMoreIcon/></Button>
                    </Grid>

                    <Collapse in={this.state.coilExp} timeout="auto" unmountOnExit={true}>
                      <Typography>
                        <ol>
                          <li>Arrive to operating room where sedating medications are given.</li>
                          <li>Catheter is inserted in an artery in the leg. A catheter is a small, flexible tube.</li>
                          <li>Catheter travels through the arteries in the body to the aneurysm.</li>
                          <li>Metal coils are placed in the aneurysm through the catheter. The aneurysm is packed fully with coils.</li>
                          <li>The catheter is removed, leaving the coils behind.</li>
                        </ol>
                      </Typography>
                    </Collapse>
                    <Typography variant={'body1'}>
                      <b>Recovery:</b>
                      <ul>
                        <li>Stay overnight in hospital intensive care unit with leg immobilized. You will be awake.</li>
                        <li>A small scar will may form on the inner thigh where the catheter was placed.</li>
                        <li>Brain imaging and then clinic visit several weeks after to ensure that the aneurysm has been cured.</li>
                      </ul>
                    </Typography>
                  </Paper>
                </Grid>
                <Grid item={true} xs={12} sm={6}>
                  <Paper className={'norm_padding'}>
                    <Typography variant={'title'} gutterBottom={'true'}>Open Surgery</Typography>
                    <Typography variant={'subtitle1'} gutterBottom={'true'}>A surgery done throuh an opening made in the skull.</Typography>
                    <img src={lab_clipping_img}/>

                    <Grid alignItems={'center'} container={true} justify={'space-between'}>
                      <Typography variant={'body1'}>
                        <b>Procedure:</b>
                        takes about several hours.
                      </Typography>
                      <Button onClick={() => this.toggleClipExp()}><ExpandMoreIcon className={this.state.clipExp
        ? 'rotate-180 now'
        : 'rotate-180'}/></Button>
                    </Grid>

                    <Collapse in={this.state.clipExp} timeout="auto" unmountOnExit={true}>
                      <Typography variant={'body1'}>
                        <ol>
                          <li>Arrive to operating room where you will put to sleep under general anesthesia</li>
                          <li>An opening is made by removing some of the bone in the skull.</li>
                          <li>The brain is spread so that the surgeon can see the aneurysm.</li>
                          <li>A metal clip is placed around the aneurysm, pinching it closed from the artery.</li>
                          <li>All tools are removed, leaving the clip behind.</li>
                          <li>The bone that was removed earlier is put back and fixed in place with screws, closing the opening in the skull.
                          </li>
                        </ol>
                      </Typography>
                    </Collapse>

                    <Typography variant={'body1'}>

                      <b>Recovery:</b>
                      <ul>
                        <li>Stay for a few days in the hospital beginning in the intensive care unit and then move to a normal hospital room when you are stable.</li>
                        <li>A scar may form at where the skin was cut to make an opening in the skull. Hair may have been cut to decrease the risk of infection.</li>
                        <li>Brain imaging and then clinic visit several weeks after to ensure that the aneurysm has been cured.</li>
                      </ul>
                    </Typography>
                  </Paper>
                </Grid>
              </Grid>
            </Grid>
            <Divider/>
            <Grid className={'content-block'} item = {
                true
              }
              xs = {
                12
              }
              sm = {
                8
              }
              md = {
                6
              } > <Typography variant={"title"} gutterBottom={true}>Recovering from Surgery:</Typography>
              <Typography variant={'body1'} paragraph={true}>
                Even if a surgery goes well, it can take some time to recover, and each person is different. Many people who have had aneurysms treated recommend that you take time to ensure that you are feeling well before returning to full-time work.
              </Typography>
            </Grid>
            <Divider/>
            <Grid item={true} xs={12} sm={8} md={6} className={'content-block'}>
              <Typography variant={"title"} gutterBottom={true}>Risks of Surgery:</Typography>
              <Typography variant={'body1'} paragraph={true}>
                Although the two surgery options are different, the kinds of risks associated with them include:
                <ul>
                  <li>Bleeding during surgery.</li>
                  <li>
                    <a onClick={this.handleStrokeDefinitionOpen}>Stroke</a>
                    after surgery.</li>
                  <li>Infection in the brain.</li>
                </ul>
              </Typography>
              <Definition word={'stroke'} open={this.state.showStrokeDefinition} onClose={this.handleStrokeDefinitionClose}/>
            </Grid>

          </Grid>
        </Paper>
      </Grid>
    </div>)}};;

export default AboutSurg;
